<template>
	<div class="setAvatar">
        <!-- 头部 -->
        <div class="nav_top d-flex jc-between ai-center bg-white">
            <div> 
                <el-button type="text" @click="$router.push('/user/userSetting')">
                    <div class="iconfont icon-back ml-1 fs-xxxl text-dark"></div>
                </el-button>
            </div>
            <div class="fs-xxl text-dark pr-3">
                选择头像
            </div>
            <div></div>
        </div>

        <div class="avatar d-flex jc-between flex-wrap px-3 py-3">
            <div class="px-3 py-3" v-for="o in 6" style="width: 33%" @click="use(item.url)">
                <el-image :src="url" style="width: 6.5rem; height: 6.5rem; border-radius: 50%">
                    
                </el-image>
            </div>
        </div>
      

	</div>
</template>

<script>
import { Toast } from "mint-ui";
	export default {
		data() {
			return {
				url: '',
                avatarList: [],

			}
		},
		methods: {
           async user(url){
           	  const res = await this.$http.post()
              if (res.data.code != 0) {
                this.$router.push('/user/userSetting')
                Toast('修改成功')
              }
           },
           async getAvatar(){
              const res = await this.$http.get()
              if (res.data.code != 0) {
                this.avatarList = res.data
              }else{
                Toast('网络异常')
              }
           },

		},
        created(){

        },

	}
</script>

<style lang="scss" scoped>
	.el-button {
		width: 100%;
		
	}

</style>